body {
  padding: 0px;
  margin: 0px;
  width: 100vw;
  height: 100vh;
  position: relative;
  /* display: inline-block; */
}
#body-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  width: 100vw;
  background-size: contain;
  background-color: rgba(249, 249, 247, 0.936);
  background-image: repeating-linear-gradient(
    to left top,
    rgba(224, 70, 70, 0.863),
    rgba(224, 172, 15, 0.7) 5px
  );
}
.display-upper {
  height: 30vh;
  width: 100%;
  position: absolute;
  opacity: 1;
  z-index: 10;
  top: 40vh;
  text-align: center;
  color: yellow;
}
.display-upper button {
  background-color: rgb(124, 124, 141);
  font-size: 200%;
  font-weight: 600;
  border-radius: 2rem;
  padding: 0.4rem;
  box-shadow: 0px 0px 4px black;
}
/*container of the game*/
.container {
  width: 100%;
  height: 70%;
  box-sizing: border-box;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  border-left: 2px solid black;
  border-right: 2px solid black;
  margin: 0px;
  margin-top: 15vh;
  margin-bottom: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  background-color: #0d242dd1;
  background-image: repeating-linear-gradient(
    to right top,
    rgba(63, 4, 4, 0.863),
    rgba(12, 60, 100, 0.7) 5px
  );
}
/*Rods and ball of the game*/
.rod1,
.rod2 {
  background-color: rgb(249, 212, 23);
  width: 150px;
  height: 20px;
  position: relative;
  left: 45%;
  border: 1px solid white;
  border-radius: 40rem;
  box-sizing: border-box;
  font-size: 15px;
  text-align: center;
}
.rod2 {
  background-color: rgb(238, 73, 18);
}
.ball {
  background-color: rgb(246, 4, 44);
  width: 34px;
  height: 34px;
  border-radius: 50%;
  position: absolute;
  align-self: center;
  box-sizing: border-box;
  border: 2px solid black;
}
.hidden {
  visibility: hidden;
}
/*center-rod*/
.center-rod {
  left: 45%;
}
/*blurry*/
.blurry {
  opacity: 0.3;
}
.top-container {
  display: flex;
  justify-content: space-between;
  height: 10%;
  background-size: contain;
  box-sizing: border-box;

  align-items: baseline;
}
.top-container > div {
  height: 20px;
  padding: 0px;
  margin-left: 10px;
  margin-right: 10px;
}
.score {
  display: flex;
  padding: 0px;
  margin: 0px;
  font-weight: 700;
}
.score h5 {
  font-size: 100%;
  font-weight: 700;
}
.new-game button {
  background-color: #070797;
  font-size: 100%;
  font-weight: 700;
  padding: 0.2rem;
  box-shadow: 0px 0px 4px black;
  color: #ffffff;
}
/*Lives score*/

.lives {
  color: #da1818f2;
  font-weight: 900;
}
.lives i {
  color: red;
  font-size: 1rem;
}
